import { Component, Input, OnInit } from "@angular/core";
import { NumberDisplayFormatterService } from "../../services/number-display-formatter.service";

@Component({
    // eslint-disable-next-line @angular-eslint/component-selector
    selector: "show-improvement",
    templateUrl: "./show-improvement.component.html"
})
export class ShowImprovementComponent implements OnInit {

    @Input()
    improvement: number;

    @Input()
    periodLengthInDays: number;

    improvementFormatted: string;

    // eslint-disable-next-line no-empty, @typescript-eslint/no-empty-function
    constructor(private numberDisplayFormatter: NumberDisplayFormatterService) {
    }

    ngOnInit() {
        this.improvementFormatted = this.numberDisplayFormatter.roundPercentage(this.improvement);
    }
}
