import { Injectable } from "@angular/core";

@Injectable()
export class NumberDisplayFormatterService {
    public roundPercentage(percentage: number) {
        if (percentage === null)
            return;

        const absValue = Math.abs(percentage);
        if (absValue === 0)
            return percentage.toFixed(0);
        if (absValue < 10)
            return percentage.toFixed(2);
        if (absValue < 100)
            return percentage.toFixed(1);
        else {
            return percentage.toFixed(0);
        }
    }
}
