import { finalize, map, mergeMap } from "rxjs/operators";
import { Component, OnInit, Input, ViewEncapsulation } from "@angular/core";
import { SitefinityInsightTouchpointRequesterService } from "../../../shared/insight/services/sitefinity-insight-touchpoints-requester.service";
import { SitefinityInsightConversionsRequesterService } from "../../services/sitefinity-insight-conversions-requester.service";
import { Touchpoint } from "../../entities/touchpoint";
import { AttributionWithGoalInfo } from "./attribution-with-goal-info";
import { GoalPeriodDisplayService } from "../../services/goal-period-display.service";
import { NumberDisplayFormatterService } from "../../services/number-display-formatter.service";

@Component({
    // eslint-disable-next-line @angular-eslint/component-selector
    selector: "touchpoints-attribution-table",
    templateUrl: "./touchpoint-hightest-attribution-table.component.html",
    styleUrls: ["./touchpoint-hightest-attribution-table.component.scss"],
    encapsulation: ViewEncapsulation.None
})
export class TouchpointHighestAttributionTableComponent implements OnInit {
    @Input()
    touchpoint: Touchpoint;

    attributionsWithGoalNames: AttributionWithGoalInfo[];
    loadingData = true;
    httpError: any = null;

    constructor(
        readonly numberDisplayFormatter: NumberDisplayFormatterService,
        private readonly touchpointRequester: SitefinityInsightTouchpointRequesterService,
        private readonly conversionRequester: SitefinityInsightConversionsRequesterService
        ) {
    }

    ngOnInit() {
        this.getAttributions();
    }

    getAttributions() {
        this.httpError = null;
        this.loadingData = true;
        this.touchpointRequester.getAttributions(this.touchpoint.Id, 3).pipe(
            mergeMap((attributions) => {
                const goalIds = attributions.map(a => a.GoalId);

                return this.conversionRequester.getAllGoals(goalIds).pipe(
                    map((goals) => {
                        return {attributions, goals};
                    }));
            }),
            map( (attributionsAndGoals) => {
                const attributions = attributionsAndGoals.attributions;
                const goals = attributionsAndGoals.goals;
                const goalIdToGoalEntityMap = {};

                for (const goal of goals) {
                    goalIdToGoalEntityMap[goal.Id] = { Name: goal.Name, InputDays: goal.InputDays };
                }

                const attributionsWithGoalInfo = attributions.map( (attribution: AttributionWithGoalInfo) => {
                    const goal = goalIdToGoalEntityMap[attribution.GoalId];
                    attribution.GoalName = goal.Name;
                    attribution.GoalPeriod = GoalPeriodDisplayService.getDisplayPeriod(goal.InputDays);

                    return attribution;
                });

                return attributionsWithGoalInfo;
            }),
            finalize(() => this.loadingData = false))
            .subscribe(attributionsWithGoalNames => {
                this.attributionsWithGoalNames = attributionsWithGoalNames;
            }, (error) => {
                this.httpError = error;
            });
    }
}
