import { finalize } from "rxjs/operators";
import { Component, OnInit, Input, ViewEncapsulation } from "@angular/core";
import { Touchpoint } from "../../entities/touchpoint";
import { KendoChartDataConverterService } from "./kendo-chart-data-converter.service";
import { TouchpointStatisticsChartDataSource } from "./touchpoint-statistics-chart-data-source";
import { Rect } from "@progress/kendo-drawing";

const FAKE_DATA_BAR_COLOR: string = "#eee";
@Component({
    // eslint-disable-next-line @angular-eslint/component-selector
    selector: "touchpoint-chart",
    templateUrl: "./touchpoint-chart.component.html",
    styleUrls: ["./touchpoint-chart.component.scss"],
    encapsulation: ViewEncapsulation.None
})
export class TouchpointChartComponent implements OnInit {
    @Input()
    touchpoint: Touchpoint;
    @Input()
    periodLengthInDays: number;

    kendoChartDataSource: TouchpointStatisticsChartDataSource = new TouchpointStatisticsChartDataSource();

    loadingData = true;
    httpError: any = null;

    constructor(private readonly kendoChartDataConverterService: KendoChartDataConverterService) {
            this.valueAxisValueToToShortMagnituteDisplay = this.valueAxisValueToToShortMagnituteDisplay.bind(this);
    }

    ngOnInit() {
        this.getTouchpointStatistics();
    }

    getTouchpointStatistics() {
        this.httpError = null;
        this.loadingData = true;

        this.kendoChartDataConverterService.getTouchpointStatisticsDataSource(this.touchpoint.Id, this.periodLengthInDays).pipe(
            finalize(() => this.loadingData = false))
            .subscribe((kendoChartDataSource) => {
                this.kendoChartDataSource = kendoChartDataSource;
            }, (error) => {
                this.httpError = error;
            });
    }

    adjustZeroBarOffset(e) {
        const ZERO_BAR_SIZE = 1;

        let visual;
        if (e.value === 0 || e.rect.size.height < ZERO_BAR_SIZE) {
          e.rect.origin.y -= ZERO_BAR_SIZE;
          e.rect.size.height = ZERO_BAR_SIZE;

          visual = new Rect(e.rect, {
            fill: {
              color: e.options.color
            },
            stroke: {
                color: e.options.color,
                width: 1
            }
          });
        } else {
          visual = e.createVisual();
        }

        return visual;
    }

    valueAxisValueToToShortMagnituteDisplay(valueAxisItem) {
        const valueAxisValue = valueAxisItem.value;
        // valueAxisItem.value is each label on the Y axis(Value Axis). It is calculated by Kendo.
        // When values on the charts are relatively large, the valueAxisItem.value tend to be round numbers
        // for example there are point with values >= 500 and <= 1000, the label values will be 0, 500, 1000
        return this.kendoChartDataConverterService.formatNumberByIncludingMagnituteNotation(valueAxisValue);
    }

    getBarColor(bar) {
        if (bar.dataItem.IsFake)
            return FAKE_DATA_BAR_COLOR;
        else
            return "#38AB63";
    }

    highlightVisual(bar) {
        let highlight = FAKE_DATA_BAR_COLOR;
        if (!bar.dataItem.IsFake) {
            highlight = "#309255";
        }
        const visual = bar.createVisual();
        visual.fill(highlight);
        visual.stroke(highlight);
        visual.stroke().opacity = 1;

        return visual;
    }
}
