import { map } from "rxjs/operators";
import { Injectable } from "@angular/core";
import { TouchpointStatistic } from "../../entities/touchpoint-statistics";
import { SitefinityInsightTouchpointRequesterService } from "../../../shared/insight/services/sitefinity-insight-touchpoints-requester.service";

import { TouchpointStatisticsItem } from "./touchpoint-statistics-item";
import { TouchpointStatisticsChartDataSource } from "./touchpoint-statistics-chart-data-source";

import { DateTimeUtilsService } from "../../../shared/insight/services/date-time-utils.service";
import { Observable } from "rxjs";
import { stringConstants } from "../../../../common-constants";

@Injectable()
export class KendoChartDataConverterService {
    constructor(
        private readonly sitefinityInsightServerService: SitefinityInsightTouchpointRequesterService,
        private readonly dateTimeUtils: DateTimeUtilsService
    ) { }

    formatNumberByIncludingMagnituteNotation(num: number): string {
        const oneK = 1000;
        const oneM = 1000 * 1000;

        if (Math.abs(num) < oneK) {
            return `${num}`;
        } else if (Math.abs(num) < oneM) {
            return toFixedIfNotInteger(num / oneK) + "K";
        } else {
            return toFixedIfNotInteger(num / oneM) + "M";
        }

        function toFixedIfNotInteger(num) {
            if (Number.isInteger(num))
                return `${num}`;
            else
                return num.toFixed(1);
        }
    }

    getTouchpointStatisticsDataSource(touchpointId: number, periodInDays: number): Observable<TouchpointStatisticsChartDataSource> {
        return this.sitefinityInsightServerService.getDailyStatistics(touchpointId, periodInDays).pipe(
            map( (touchpointStatistics: TouchpointStatistic[]) => {
                return touchpointStatistics.map((stat) => this.touchpointStatisticToKendoChartItem(stat));
            }),
            map( (chartDataItemsForStatistics: TouchpointStatisticsItem[]) => {
                while (chartDataItemsForStatistics.length < periodInDays) {
                    const latestStatisticsDate = chartDataItemsForStatistics[chartDataItemsForStatistics.length - 1].StatisticsDate;
                    const fillerStatisticsDate = this.dateTimeUtils.addDays(new Date(latestStatisticsDate), 1).toISOString();
                    const chartDataItem = this.touchpointStatisticToKendoChartItem({ StatisticsDate: fillerStatisticsDate, UniqueHits: 0, TotalHits: 0 });
                    chartDataItem.IsFake = true;
                    chartDataItemsForStatistics.push(chartDataItem);
                }

                return chartDataItemsForStatistics;
            }), map((chartDataItemsForStatistics) => {
                return {
                    touchpointStatisticsItems: chartDataItemsForStatistics,
                    categoryAxisLabels: KendoChartDataConverterService.getCategoryLabels(chartDataItemsForStatistics)
                };
            }));
    }

    private touchpointStatisticToKendoChartItem(touchpointStatistic: TouchpointStatistic): TouchpointStatisticsItem {
        return {
            IsFake: false,
            StatisticsDate: touchpointStatistic.StatisticsDate,
            UniqueHits: touchpointStatistic.UniqueHits,
            TotalHits: touchpointStatistic.TotalHits,
            FormattedDateForLabel: this.dateTimeUtils.toShortDateTimeFormat(touchpointStatistic.StatisticsDate),
            FormattedDateForTooltip: this.dateTimeUtils.toLongDateTimeFormat(touchpointStatistic.StatisticsDate)
        };
    }

    private static getCategoryLabels(chartsData: TouchpointStatisticsItem[]): string[] {

        const nonFakedPointsCount = chartsData.filter((x) => x.IsFake === false).length;
        if (nonFakedPointsCount < 3) {
            return KendoChartDataConverterService.produceOnlyOneLabel(chartsData);
        } else if (nonFakedPointsCount <= 15) {
            return KendoChartDataConverterService.produceTwoLabels(chartsData, nonFakedPointsCount);
        } else {
            const middlePointIndex = (nonFakedPointsCount / 2) | 0;
            return KendoChartDataConverterService.produceThreeLabels(chartsData, middlePointIndex, nonFakedPointsCount);
        }
    }

    private static produceOnlyOneLabel(chartsData) {
        const labels = [];
        for (let i = 0; i < chartsData.length; i++) {
            const stat = chartsData[i];

            if (i === 0)
                labels.push(stat.FormattedDateForLabel);
            else {
                labels.push(stringConstants.EMPTY_STRING);
            }
        }

        return labels;
    }

    private static produceTwoLabels(chartsData, nonFakedPointsCount) {
        const labels = [];
        for (let i = 0; i < chartsData.length; i++) {
            const stat = chartsData[i];
            if (i === 0)
                labels.push(stat.FormattedDateForLabel);
            else if (i === nonFakedPointsCount - 1) {
                labels.push(stat.FormattedDateForLabel);
            } else {
                labels.push(stringConstants.EMPTY_STRING);
            }
        }

        return labels;
    }

    private static produceThreeLabels(chartsData, middleLabelIndex, nonFakedPointsCount) {
        const labels = [];
        for (let i = 0; i < chartsData.length; i++) {
            const stat = chartsData[i];

            if (i === 0)
                labels.push(stat.FormattedDateForLabel);
            else if (i === nonFakedPointsCount - 1) {
                labels.push(stat.FormattedDateForLabel);
            } else if (i === middleLabelIndex) {
                labels.push(stat.FormattedDateForLabel);
            } else {
                labels.push(stringConstants.EMPTY_STRING);
            }
        }

        return labels;
    }
}
