import { forkJoin as observableForkJoin } from "rxjs";
import { finalize } from "rxjs/operators";
import { Component, OnInit, Input } from "@angular/core";
import { SitefinityInsightTouchpointRequesterService } from "../../../shared/insight/services/sitefinity-insight-touchpoints-requester.service";
import { Touchpoint } from "../../entities/touchpoint";
import { TouchpointPeriodMetrics } from "../../entities/touchpoint-period-metrics";

@Component({
    // eslint-disable-next-line @angular-eslint/component-selector
    selector: "touchpoint-hits",
    templateUrl: "./touchpoint-hits.component.html"
})
export class TouchpointHitsComponent implements OnInit {
    @Input()
    touchpoint: Touchpoint;
    @Input()
    periodLengthInDays: number;
    @Input()
    periodString: string;

    touchpointPeriodMetrics: TouchpointPeriodMetrics;
    hasStatisticsOutsideOfTheRequestedPeriod: boolean = false;
    hasStatisticsForEntirePeriod: boolean = false;
    loadingData = true;
    httpError: any = null;

    constructor(private readonly sitefinityInsightServerService: SitefinityInsightTouchpointRequesterService) {
    }

    ngOnInit() {
        this.getTouchpointHits();
    }

    getTouchpointHits() {
        this.loadingData = true;
        this.httpError = null;
        const extendedPeriod = this.periodLengthInDays + 1;
        const dailyStatistics$ = this.sitefinityInsightServerService.getDailyStatistics(this.touchpoint.Id, extendedPeriod);
        const periodMetrics$ = this.sitefinityInsightServerService.getPeriodMetrics(this.touchpoint.Id, this.periodString);

        observableForkJoin(dailyStatistics$, periodMetrics$).pipe(
            finalize(() => this.loadingData = false))
            .subscribe(([dailyStatistics, touchpointPeriodMetrics]) => {
                this.touchpointPeriodMetrics = touchpointPeriodMetrics;
                this.hasStatisticsOutsideOfTheRequestedPeriod = dailyStatistics.length >= extendedPeriod;
                this.hasStatisticsForEntirePeriod = dailyStatistics.length >= this.periodLengthInDays;
            }, (error) => {
                this.httpError = error;
            });
    }
}
