import { Component, ElementRef, OnInit } from "@angular/core";
import { Touchpoint } from "../../entities/touchpoint";
import { ContentItemInfo } from "../../../shared/insight/interfaces/content-item-info";
import { DetailsViewState } from "./details-view-state";
import { SitefinityInsightTouchpointRequesterService } from "../../../shared/insight/services/sitefinity-insight-touchpoints-requester.service";
import { ActivatedRoute } from "@angular/router";
import { Observable } from "rxjs";
import { ModalWindowService } from "../../../shared/services/modal-window.service";
import { SiteData } from "../../../shared/services/metadata/site-data.store";
import { INSIGHT } from "../../../shared/insight/insight.constants";

@Component({
    templateUrl: "./insights-details-view.component.html",
    styleUrls: ["../../../../../styles/components/_modal-window.scss"]
})
export class InsightsDetailsViewComponent implements OnInit {
    contentItemInfo: ContentItemInfo; // injected from the InsightsCellComponent
    externalTouchpointLoad$?: Observable<Touchpoint>; // injected from the InsightsCellComponent
    notifyGridCellForNewTouchpointFunc: (tp: Touchpoint) => void; // injected from the InsightsCellComponent
    siteDataStore: SiteData;
    viewState: DetailsViewState = new DetailsViewState();
    touchpoint: Touchpoint;
    httpError: any;

    constructor(
        private readonly decServerService: SitefinityInsightTouchpointRequesterService,
        private readonly route: ActivatedRoute,
        private modalWindowService: ModalWindowService,
        private ref: ElementRef) {

        const test = this.modalWindowService.getData(INSIGHT).componentData.properties as any;
        this.externalTouchpointLoad$ = test.externalTouchpointLoad$;
        this.contentItemInfo = test.contentItemInfo;
        this.notifyGridCellForNewTouchpointFunc = test.notifyGridCellForNewTouchpointFunc;
    }

    onCloseClicked() {
        this.modalWindowService.close();
    }

    ngOnInit() {
        this.ref.nativeElement.parentElement.classList.add("sf-insight-window");
        this.siteDataStore = this.route.parent.parent.snapshot.data.siteDataStore;
        this.initViewState();
    }

    initViewState() {
        this.httpError = null;

        this.getTouchpoint().subscribe((touchpoint) => {
            this.touchpoint = touchpoint;

            if (!this.touchpoint) {
                this.viewState.showCreateTouchpointUI();
            } else if (this.touchpoint) {
                this.decServerService.hasStatistics(this.touchpoint.Id)
                    .subscribe((hasStatistics: boolean) => {
                        if (hasStatistics)
                            this.viewState.showTouchpointDataUI();
                        else
                            this.viewState.showNoStatisticsYetUI();
                    }, (error) => {
                        this.httpError = error;
                    });
            }
        }, (error) => {
            this.httpError = error;
        });
    }

    onTouchpointCreated(touchpoint: Touchpoint) {
        this.touchpoint = touchpoint;
        this.notifyGridCellForNewTouchpointFunc(this.touchpoint);
    }

    private getTouchpoint() {
        if (this.externalTouchpointLoad$)
            return this.externalTouchpointLoad$;

        return this.decServerService.getTouchpointByContent(this.contentItemInfo);
    }
}
