export class DetailsViewState {
    shouldShowCreateTouchpointUI: boolean;
    shouldShowNoStatisticsYetUI: boolean;
    shouldShowTouchpointDataUI: boolean;

    constructor() {
        this.shouldShowCreateTouchpointUI = false;
        this.shouldShowNoStatisticsYetUI = false;
        this.shouldShowTouchpointDataUI = false;
    }

    public showCreateTouchpointUI() {
        this.restartState();
        this.shouldShowCreateTouchpointUI = true;
    }

    public showNoStatisticsYetUI() {
        this.restartState();
        this.shouldShowNoStatisticsYetUI = true;
    }

    public showTouchpointDataUI() {
        this.restartState();
        this.shouldShowTouchpointDataUI = true;
    }

    private restartState() {
        for (const shouldShowKey of Object.keys(this)) {
            this[shouldShowKey] = false;
        }
    }
}