import { Component, OnInit, Input } from "@angular/core";
import { ConnectorService } from "../../../shared/insight/services/connector.service";
import { SitefinityInsightDatacenterRequesterService } from "../../services/sitefinity-insight-datacenter-requester.service";

@Component({
    // eslint-disable-next-line @angular-eslint/component-selector
    selector: "sitefinity-insight-analytics-redirect-button",
    templateUrl: "./sitefinity-insight-analytics-redirect-button.component.html"
})
export class SitefinityInsightAnalyticsRedirectButtonComponent implements OnInit {
    @Input()
    redirectPath: string;
    @Input()
    buttonText: string;

    sitefinityInsightWebAppRedirectUrl: string;
    loadingData = false;
    httpError: any = null;

    constructor(
        private readonly connectorService: ConnectorService,
        private readonly sitefinityInsightDatacenterService: SitefinityInsightDatacenterRequesterService) { }

    ngOnInit() {
        this.getWebAppUrl();
    }

    getWebAppUrl() {
        this.httpError = null;
        this.loadingData = true;
        this.connectorService.getConnectorDetails()
            .subscribe((sitefinityInsightConfiguration) => {
                this.sitefinityInsightDatacenterService.getDatacenterId(sitefinityInsightConfiguration.DataCenterApiKey)
                    .subscribe((datacenterId) => {
                        this.sitefinityInsightWebAppRedirectUrl = `${sitefinityInsightConfiguration.DecAppUrl}/new-web-app/data-centers/${datacenterId}/${this.redirectPath}`;
                        this.loadingData = false;
                    }, (error) => {
                        this.httpError = error;
                    });
            });
    }
}
