import { finalize } from "rxjs/operators";
import { Component, OnInit, Input, EventEmitter, Output } from "@angular/core";
import { SitefinityInsightTouchpointRequesterService } from "../../shared/insight/services/sitefinity-insight-touchpoints-requester.service";
import { ContentItemInfo } from "../../shared/insight/interfaces/content-item-info";
import { Touchpoint } from "../entities/touchpoint";
import { CreateTouchpointViewState } from "./create-touchpoint-view-state";
import { SiteData } from "../../shared/services/metadata/site-data.store";
import { stringConstants } from "../../../common-constants";

@Component({
    // eslint-disable-next-line @angular-eslint/component-selector
    selector: "create-content-touchpoint",
    templateUrl: "./create-content-touchpoint.component.html",
    styleUrls: ["./create-content-touchpoint.component.scss"]
})
export class CreateContentTouchpointComponent implements OnInit {
    static readonly PASCAL_CASE_GROUP_REGEX: RegExp = /($[a-z])|[A-Z][^A-Z]+/g;

    viewState: CreateTouchpointViewState = new CreateTouchpointViewState();
    createdTouchpoint: Touchpoint;

    loadingData: boolean = false;
    httpError: any;
    contentTypeDisplayName: string;

    @Input()
    contentItemInfo: ContentItemInfo;

    @Input()
    siteDataStore: SiteData;

    // eslint-disable-next-line
    @Output("close")
    closeButtonPressedEvent: EventEmitter<void> = new EventEmitter();

    // eslint-disable-next-line
    @Output("touchpointCreated")
    touchpointCreatedEvent: EventEmitter<Touchpoint> = new EventEmitter();

    constructor(private readonly sitefinityInsightServerService: SitefinityInsightTouchpointRequesterService) {
    }

    ngOnInit() {
        this.contentTypeDisplayName = this.contentItemInfo.ContentType.match(
            CreateContentTouchpointComponent.PASCAL_CASE_GROUP_REGEX).join(" ").toLowerCase();
        this.viewState.showCreateTouchpointUI();
    }

    cancel() {
        this.closeButtonPressedEvent.emit(null);
    }

    createTouchpoint() {
        this.httpError = stringConstants.EMPTY_STRING;
        this.loadingData = true;
        this.sitefinityInsightServerService.createContentTouchpoint(this.siteDataStore.DefaultContentCulture, this.contentItemInfo).pipe(
        finalize(() => this.loadingData = false))
        .subscribe((touchpoint) => {
            this.createdTouchpoint = touchpoint;
            this.viewState.showSuccessfullyCreatedUI();
            this.touchpointCreatedEvent.emit(this.createdTouchpoint);
        }, (error) => {
            this.httpError = error;
        });
    }

    close() {
        this.closeButtonPressedEvent.emit(null);
    }
}
