export class CreateTouchpointViewState {

    shouldShowCreateTouchpointUI: boolean;
    shouldShowSuccessfullyCreatedUI: boolean;

    constructor() {
        this.restartState();
    }

    showCreateTouchpointUI() {
        this.restartState();
        this.shouldShowCreateTouchpointUI = true;
    }

    showSuccessfullyCreatedUI() {
        this.restartState();
        this.shouldShowSuccessfullyCreatedUI = true;
    }

    private restartState() {
        for (const shouldShowKey of Object.keys(this)) {
            this[shouldShowKey] = false;
        }
    }
}