export class GoalPeriodDisplayService {
    static getDisplayPeriod(period) {
        let displayPeriod = "";
        if (period > 30) {
            const periodInMonths = Math.round(period / 30);
            displayPeriod = `${periodInMonths} months`;
        } else {
            displayPeriod = `${period} days`;
        }

        return displayPeriod;
    }
}