import { map } from "rxjs/operators";
import { Injectable } from "@angular/core";
import { DataCenter } from "../entities/data-center";
import { SitefinityInsightHttpRequester } from "../../shared/insight/services/sitefinity-insight-http-requester";
import { HttpDataResponseUtils } from "./http-data-response-utils";
import { CollectionResponse } from "./collection-response";

@Injectable()
export class SitefinityInsightDatacenterRequesterService {
    constructor(private sitefinityInsightHttpClient: SitefinityInsightHttpRequester) {
    }

    getDatacenterId(apiKey: string) {
        const headers = {
            filter: { ApiKey: apiKey }
        };

        return this.sitefinityInsightHttpClient.get<CollectionResponse<DataCenter>>("admin/v1/users/mydatacenters", headers).pipe(
            map((datacenterResult) => {
                const dataCenter = HttpDataResponseUtils.firstOrDefault(datacenterResult);

                return dataCenter.Id;
            }));
    }
}
