import { AfterViewInit, Component, Input, ViewChild } from "@angular/core";
import { ContentItemInfo } from "../../shared/insight/interfaces/content-item-info";
import { Touchpoint } from "../entities/touchpoint";
import { TouchpointHitsComponent } from "../components/touchpoint-hits/touchpoint-hits.component";
import { TouchpointChartComponent } from "../components/touchpoint-chart/touchpoint-chart.component";
import { TouchpointHighestAttributionTableComponent } from "../components/touchpoint-highest-attribution-table/touchpoint-hightest-attribution-table.component";
import { SitefinityInsightAnalyticsRedirectButtonComponent } from "../components/sitefinity-insight-analytics-redirect-button/sitefinity-insight-analytics-redirect-button.component";
import { SiteData } from "../../shared/services/metadata/site-data.store";

@Component({
    // eslint-disable-next-line @angular-eslint/component-selector
    selector: "touchpoint-details",
    templateUrl: "./touchpoint-details.component.html"
})
export class TouchpointDetailsComponent implements AfterViewInit {
    @Input()
    touchpoint: Touchpoint;

    @Input()
    contentItemInfo: ContentItemInfo;

    @Input()
    siteDataStore: SiteData;

    @ViewChild(TouchpointHitsComponent) hitsComponent: TouchpointHitsComponent;

    @ViewChild(TouchpointChartComponent) chartComponent: TouchpointChartComponent;

    @ViewChild(TouchpointHighestAttributionTableComponent) attributionsComponent: TouchpointHighestAttributionTableComponent;

    @ViewChild(SitefinityInsightAnalyticsRedirectButtonComponent) redirectButtonComponent: SitefinityInsightAnalyticsRedirectButtonComponent;

    periodLengthInDays = 30;
    periodString = "30 days";
    private subcomponentsLoaded = false;

    ngAfterViewInit() {
        setTimeout(() => this.subcomponentsLoaded = true, 0);
    }

    hasErrorInAnyComponent() {
        return this.subcomponentsLoaded &&
                (this.hitsComponent.httpError ||
                this.chartComponent.httpError ||
                this.attributionsComponent.httpError ||
                this.redirectButtonComponent.httpError);
    }

    isLoadingAnyComponent() {
        return this.subcomponentsLoaded &&
                (this.hitsComponent.loadingData ||
                this.chartComponent.loadingData ||
                this.attributionsComponent.loadingData ||
                this.redirectButtonComponent.loadingData);
    }

    retryLoadingComponents() {
        if (this.subcomponentsLoaded) {
            this.hitsComponent.getTouchpointHits();
            this.chartComponent.getTouchpointStatistics();
            this.attributionsComponent.getAttributions();
            this.redirectButtonComponent.getWebAppUrl();
        }
    }
}
