import { map } from "rxjs/operators";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { CollectionResponse } from "./collection-response";
import { SitefinityInsightHttpRequester } from "../../shared/insight/services/sitefinity-insight-http-requester";
import { HttpDataResponseUtils } from "./http-data-response-utils";
import { Conversion } from "../entities/conversion";

@Injectable()
export class SitefinityInsightConversionsRequesterService {

    constructor(private sitefinityInsightHttpClient: SitefinityInsightHttpRequester) {
    }

    getAllGoals(goalIds?: number[]): Observable<Conversion[]> {

        const headers: any = {};
        if (goalIds)
            headers.filter = {Id: goalIds};

        return this.sitefinityInsightHttpClient.get<CollectionResponse<Conversion>>("analytics/v1/goals", headers).pipe(
                                    map(HttpDataResponseUtils.flattenCollectionResponse));
    }
}
